import { getIntelBriefings } from '@/lib/file-system';
import { IntelGrid } from '@/components/intel/IntelGrid';
import type { Vertical } from '@/lib/types';

export const dynamic = 'force-dynamic';

interface IntelPageProps {
  searchParams: Promise<{ verticals?: string }>;
}

export default async function IntelPage({ searchParams }: IntelPageProps) {
  const params = await searchParams;

  // Parse vertical filters from URL
  const selectedVerticals = params.verticals?.split(',').filter(Boolean) as Vertical[] | undefined;

  // Fetch intel briefings (filter by first selected vertical if any)
  const briefings = await getIntelBriefings(30, selectedVerticals?.[0]);

  // Filter by all selected verticals if multiple selected
  const filteredBriefings = selectedVerticals?.length
    ? briefings.filter((b) => selectedVerticals.includes(b.vertical))
    : briefings;

  return (
    <div className="space-y-8">
      {/* Header */}
      <div>
        <h1 className="text-3xl font-bold text-white">Intel Room</h1>
        <p className="text-white/60 mt-1">
          Market intelligence briefings and competitive analysis
        </p>
      </div>

      {/* Intel Grid */}
      <IntelGrid briefings={filteredBriefings} />
    </div>
  );
}
